.contigent-container {
    width: 100vw;
    height: calc(100vh - var(--navbar-height));
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
    padding-top: 5rem;
    padding-right: 3rem;
    gap: 5rem;
}

.contingent-buttons {
    gap: .8em;
    display: flex;
    padding: 1rem;
}

.contingent-buttons button {
    border: none;
    outline: none;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1.3rem;
    padding: 0.5rem 1rem;
    border: 3px solid #F9F4F4;
    background-color: rgba(35, 7, 7, 0.58);
    color: #ECE3A6;
    filter: drop-shadow(0px 0px 10px #FF00E5);
    box-shadow: inset 0px 4px 15px 4px #FF00E5;

    /* font-family: gemstone, sans-serif; */
}
.contigent-form {
    width: 60vw;
    height: 40vh;
    /* padding: 2rem; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.4);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
}

.contingent-id-input {
    margin: 2em auto;
    display: flex;
    flex-direction: column;
    /* justify-content: space-around; */
    /* height: 50vh; */
}

.contingent-id-input-content, .contingent-code-input-content{
  margin: .8rem;
}

.contigent-input {
    padding: 0.5rem;
    height: 2.5rem; /* Adjust the height as needed */
  }
  

