.signup-modal {
	display: none;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	align-items: center;
}
.details-sigunp{
	color: #007bff;
	/* border-bottom: solid white 0.1rem; */
	text-decoration: underline;
	text-align: left;
	height: 3rem;
}
.captchas {
	display: flex;
	justify-content: center;
	align-items: center;
	scale: calc( 80 / 90 );
}

.signup-close {
	position: absolute;
	top: 1rem;
	right: 1rem;
}

.signup-modal.open {
	display: flex;
}

.signup-box {
	background: var(--modal-background-color);
	padding: 1.5rem;
	text-align: center;
	border-radius: 8px;
	width: 90vw;
	height: 70vh;
	overflow-y: auto;
	position: relative;
}

.signup-box h2 {
	margin: 1rem 0;
	padding: 0;
	color: #fff;
	text-align: center;
}

.user-box {
	position: relative;
	margin-bottom: 1rem;
}

.user-box label input h1 {
	color: #fff;
}


.user-box input, .user-box select {
	width: 80vw;
	padding: 10px 0;
	font-size: 16px;
	color: white;
	margin-bottom: 10px;
	border: none;
	border-bottom: 1px solid white;
	outline: none;
	background: transparent;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-background-clip: text;
    -webkit-text-fill-color: white;
    transition: background-color 5000s ease-in-out 0s;
    box-shadow: inset 0 0 20px 20px #00000000;
}

#yop,
#dob,
#gender ,
#name,
#email,
#number,
#date,
#state,
#city,
#text,
#password
{
	text-align: right;
}

.user-box label {
	position: absolute;
	top: 0;
	left: 0;
	padding: 10px 0;
	font-size: 16px;
	color: white;
	pointer-events: none;
	transition: 0.5s;
}

.user-box input:focus~label,
.user-box input:valid~label,
.user-box select:focus~label,
.user-box select:valid~label {
	top: -20px;
	left: 0;
	color: #007bff;
	font-size: 12px;
}

input[type="date"]::-webkit-calendar-picker-indicator {
	background: transparent;
	bottom: 0;
	color: transparent;
	cursor: pointer;
	height: auto;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	width: auto;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
.error {
	color: red;
	font-size: 14px;
	margin-top: 5px;
}


/* button animation */
.submit-button {
	position: relative;
	display: inline-block;
	padding: 10px 20px;
	color: #03e9f4;
	text-decoration: none;
	text-transform: uppercase;
	overflow: hidden;
	transition: 0.5s;
	letter-spacing: 2px;
	cursor: pointer;
	border: none;
	background: transparent;
	transition: 0.3s ease;
    border-radius: 5px;
}

.submit-button:active {
	scale: 0.9;
	background: #03e9f4;
    color: #fff;
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 100px #03e9f4;
}

.submit-button span {
	position: absolute;
	display: block;
}

.submit-button span:nth-child(1) {
	top: 0;
	left: -100%;
	width: 100%;
	height: 2px;
	background: linear-gradient(90deg, transparent, #03e9f4);
	animation: btn-anim1 1s linear infinite;
}

.submit-button span:nth-child(2) {
	top: -100%;
	right: 0;
	width: 2px;
	height: 100%;
	background: linear-gradient(180deg, transparent, #03e9f4);
	animation: btn-anim2 1s linear infinite;
	animation-delay: 0.25s;
}

.submit-button span:nth-child(3) {
	bottom: 0;
	right: -100%;
	width: 100%;
	height: 2px;
	background: linear-gradient(270deg, transparent, #03e9f4);
	animation: btn-anim3 1s linear infinite;
	animation-delay: 0.5s;
}

.submit-button span:nth-child(4) {
	bottom: -100%;
	left: 0;
	width: 2px;
	height: 100%;
	background: linear-gradient(360deg, transparent, #03e9f4);
	animation: btn-anim4 1s linear infinite;
	animation-delay: 0.75s;
}

@keyframes btn-anim1 {
	0% {
		left: -100%;
	}

	50%,
	100% {
		left: 100%;
	}
}

@keyframes btn-anim2 {
	0% {
		top: -100%;
	}

	50%,
	100% {
		top: 100%;
	}
}

@keyframes btn-anim3 {
	0% {
		right: -100%;
	}

	50%,
	100% {
		right: 100%;
	}
}

@keyframes btn-anim4 {
	0% {
		bottom: -100%;
	}

	50%,
	100% {
		bottom: 100%;
	}
    
}