@import url("https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700;800;900&family=Poppins:wght@400;500;600;700&display=swap");

.accomodation-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-image: url("../temp-imgs/Accommodation-bg.png");

}

.Acco_content {
  width: calc(100vw - 50px);
  padding: 10px;
  height: calc(100vh - var(--navbar-main-height));
  overflow-y: scroll;
  margin-bottom: var(--navbar-main-height);

  h3 {
    background-color: var(--accomodation-heading-background);
    color: var(--accomodation-heading-color);
    margin: 10px auto;
    padding: 1rem 0.5rem;
    font-family: var(--accomodation-heading-font);
    font-size: 2.5rem;
    text-align: center;
    border-radius: 10px;
  }

  .acco_card {
    background-color: var(--accomodation-card-background);
    color: var(--accomodation-card-color);
    font-family: var(--accomodation-card-font);

    width: 100%;
    border-radius: 10px;
    padding: 1rem;
    margin: 10px 0;
    text-align: justify;
    transition: all 0.2s ease-in-out;

    .acco_card_heading {
      text-align: left;
      font-size: 1rem;
      font-weight: 600;
    }
    .acco_card_content {
      text-align: left;
      font-size: 0.9rem;
    }
  }
}
