@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700;800;900&family=Pompiere&display=swap');

.dashboard-modal {
    font-family: var(--dashboard-modal-font);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100vw;
    height: 100vh;
    padding: 0px 10px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.dashboard-modal-box {
    width: 100%;
    padding: 10px;
    z-index: 1001;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.dashboard-modal-box__close {
    position: absolute;
    top: 2rem;
    right: 2rem;
    cursor: pointer;
    color: white;
    z-index: 1002;
}

.dashboard-modal-box__content {
    color: white;
    border-radius: 10px;
    padding: 10px;
    width: 100%;
    min-height: 30vh;
    max-height: 70vh;
    height: fit-content;
    overflow-y: auto;
    -webkit-mask: linear-gradient(180deg, transparent, white 5%, white 95%, transparent 100%);
    mask: linear-gradient(180deg, transparent, white 5%, white 95%, transparent 100%);
}

.dashboard-modal-box__content__items-list-item {
    text-align: right;
}