.event-details-card {
	background-color: rgba(243, 244, 246, 1);
	padding: 1rem;
	margin-bottom: calc(var(--navbar-height) + 1rem);
	width: 85vw;
	border-radius: 10px;
	/* margin-bottom: 1rem; */
	font-family: sans-serif;
	letter-spacing: 0;
}

.sub-event-details-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	/* gap: 1rem; */
}

.sub-event-details-image {
	width: 7rem;
	height: 7rem;
	/* aspect-ratio: 1/1; */
	border-radius: 999999px;
	background: black;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
}

.sub-event-details-image img {
	width: 5rem;
	background: cover;
}

.sub-event-details-header-content  {
	display: flex;
	flex-direction: column;
	gap: 0.3rem;
	text-align: right;
	width: calc(100% - 8rem);
	/* word-break: break-all; */
}

.sub-event-details-tagline {
	font-size: 0.8rem;
}

.sub-event-details-modal-register-button {
	background-color: #f9ca24;
	color: white;
	border: none;
	padding: 0.5rem 1rem;
	border-radius: 5px;
	font-size: 1rem;
	font-weight: 600;
	cursor: pointer;
	transition: transform 0.5s ease-in-out;
}

.sub-event-details-content {
	display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0.5rem;
    text-align: justify;
    padding: 1rem 0.5rem;
	padding-bottom: 0;
}

.sub-event-details-content h2 {
	margin-top: 0.5rem;
    text-align: center;
}

.sub-event-details-rules {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
