.landing-page {
	width: 100vw;
	background-image: url("../temp-imgs/landingpage_img1.jpeg");
	height: calc(100vh - var(--navbar-height));
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.navbar-logo{
	display: block;
}

.SF_Logo,
.IIT_KGP_LOGO {
	position: fixed;
	top: 1rem;
	width: 3.5rem;
}

.SF_Logo {
	left: 1rem;
}

.IIT_KGP_LOGO {
	right: 1rem;
}

.landing-page-buttons-container {
	width: 100vw;
	height: calc(100vh - var(--navbar-height)/2);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 1rem;
}

.landing-page-login-button,
.landing-page-dashboard-button,
.landing-page-logout-button {
	border: none;
	outline: none;
	text-decoration: none;
	border-radius: 5px;
	font-size: 1.5rem;
	padding: 0.5rem 1rem;

	/* color: white;
	background-color: var(--primary-color); */
	border: 3px solid #F9F4F4;
	background-color: rgba(35, 7, 7, 0.58);
	color: #ECE3A6;
	filter: drop-shadow(0px 0px 10px #FF00E5);
	box-shadow: inset 0px 4px 15px 4px #FF00E5;

	font-family: gemstone, sans-serif;
}

/* Login Modal CSS */
.login-modal {
	display: none;
	width: 90vw;
	height: fit-content;
	max-height: 70vh;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: var(--modal-background-color);
	overflow-y: auto;
	border-radius: 8px;
	flex-direction: column;
	align-items: center;
	padding: 1rem;
}

.login-modal.open {
	display: flex;
}

.login-close {
	position: absolute;
	top: 1rem;
	right: 1rem;
}

.login-modal h2 {
	margin: 1rem 0;
	padding: 0;
	color: #fff;
	text-align: center;
}

.login-modal form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 1rem;
}

.signup-button-div {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-top: 1rem;
}

.signup-button,
.update_password,
.signin-button {
	border: none;
	outline: none;
	text-decoration: none;
	border-radius: 5px;
	padding: 0.5rem 1rem;
	margin-top: 1rem;
	color: white;
	background-color: transparent;
	cursor: pointer;
}

.signup-button span,
.update_password span,
.signin-button span {
	color: var(--primary-color);
}